import * as React from "react"
import { graphql, useStaticQuery,Link } from "gatsby"
import { useLocation } from "@reach/router"

const Footer = ({ siteTitle }) => {
  const data = useStaticQuery(graphql`
    query {
      wp {
        themeGeneralSettings {
          emailId {
            officialEmail
          }
          locations {
            locations {
              location
            }
          }
          copyright {
            copyright
          }
        }
      }
      allWpMenu {
        nodes {
          name
          menuItems {
            nodes {
              label
              url
            }
          }
        }
      }
    }
  `)
  const email = data.wp.themeGeneralSettings?.emailId?.officialEmail
  const footerMenuOne = data.allWpMenu?.nodes[2].menuItems?.nodes ?? []
  const footerMenuTwo = data.allWpMenu?.nodes[0].menuItems?.nodes ?? []
  const footerMenuThree = data.allWpMenu?.nodes[1].menuItems?.nodes ?? []
  const locations = data.wp.themeGeneralSettings?.locations?.locations
  const copyRight = data.wp.themeGeneralSettings?.copyright
  const location = useLocation()
  return (
    <footer className="footer_block">
      <div className="footer_slider">
        <div className="footer_slider_wrapper">
          {Array.from({ length: 3 }).map((_, index) => (
            <a key={index} href={`mailto:${email}`}>
              {email}
            </a>
          ))}
        </div>
      </div>
      <div className="container">
        <div className="footer_area">
          <div className="row">
            <div className="col-5">
              <ul className="footer_menu">
                {footerMenuOne.length > 0 &&
                  footerMenuOne.map((item, index) => {
                    return (
                      <li key={index}>
                        <a href={item.url}>{item.label}</a>
                      </li>
                    )
                  })}
              </ul>
              <ul className="footer_menu">
                {footerMenuTwo.length > 0 &&
                  footerMenuTwo.map((item, index) => {
                    const isActive = location.pathname === item.url + "/"
                    return (
                      <li key={index} className={isActive ? "active" : ""}>
                        <Link to={item.url}>{item.label}</Link>
                      </li>
                    )
                  })}
              </ul>
            </div>
            <div className="col-7">
              <ul className="footer_listing">
                {footerMenuThree.length > 0 &&
                  footerMenuThree.map((item, index) => {
                    return (
                      <li key={index}>
                        <a href={item.url}>{item.label}</a>
                      </li>
                    )
                  })}
              </ul>
            </div>
          </div>
          <div className="footer_location">
            {locations.length > 0 &&
              locations.map((item, index) => {
                return <span key={index}>{item.location}</span>
              })}
          </div>
          {copyRight && <div className="copy_right">{copyRight.copyright}</div>}
        </div>
      </div>
    </footer>
  )
}

export default Footer
