import React, { useState } from "react"
import { Link, graphql, useStaticQuery } from "gatsby"
import { useLocation } from "@reach/router"

const Header = ({ siteTitle }) => {
  const [active, setActive] = useState(false)
  const handleMenu = () => {
    setActive(!active)
  }

  const data = useStaticQuery(graphql`
    query {
      wp {
        themeGeneralSettings {
          siteIdentity {
            logo {
              node {
                sourceUrl
              }
            }
          }
          emailId {
            officialEmail
          }
        }
      }
      wpMenu(name: { eq: "Header" }) {
        menuItems {
          nodes {
            label
            url
          }
        }
        name
      }
    }
  `)
  const logoUrl =
    data.wp.themeGeneralSettings?.siteIdentity?.logo?.node?.sourceUrl
  const email = data.wp.themeGeneralSettings?.emailId?.officialEmail
  const menu = data.wpMenu?.menuItems?.nodes ?? []
  const location = useLocation()
  return (
    <header className="header_block">
      <div className="container">
        <div className="header_area">
          <Link to="/" className="header_logo">
            <img
              src={logoUrl}
              alt={siteTitle}
              loading="eager"
              width={180}
              height="auto"
            />
          </Link>
          <button
            type="button"
            aria-label="hamburger"
            className={["menu_button", active && "toggle"].join(" ")}
            onClick={handleMenu}
          >
            <span />
          </button>
          <div className={["header_menu_sec", active && "slide"].join(" ")}>
            <ul className="header_menu">
              {menu.length > 0 &&
                menu.map((item, index) => {
                  const isActive = location.pathname === item.url
                  console.log(location.pathname);
                  return (
                    <li key={index} className={isActive ? "active" : ""}>
                      <Link to={item.url}>{item.label}</Link>
                    </li>
                  )
                })}
            </ul>
            <div className="header_action">
              <a href={`mailto:${email}`}>{email}</a>
              <a href="/" className="call_to_action">
                Speak to an Advisor
              </a>
            </div>
          </div>
        </div>
      </div>
    </header>
  )
}

export default Header
